body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.contenedor{
  max-width: 600px;
  margin: auto;
  border-radius: 10px;
  border-style: solid;
  border-width: thin;
  border-color: rgb(48,66,66);
  box-shadow: 5px 15px 15px grey;
}
button{
  min-width: 50px;
  margin: 2px;
}
.teclas{
  margin: 10px;
  display: inline-block;
}
.sector-teclas{
  display: flex;
}
.titulos{
  background-image: url(/appCalculadora/static/media/formulas.2156721d.jpg);
  background-repeat: no-repeat;
  background-position: cover;
  display: flex;
  margin: 10px auto;
  width: 85%;
}
img{
  max-width: 100px;
}
.mensaje{
  background-color: white;
  height: 20%;
  width: 70%;
  margin: 50px;
  padding: 15px;
  border-radius: 25px;

  font-family: Arial, Helvetica, sans-serif;
  font-style: italic;
  font-size: large;
  font-weight: bold;
  color: red;
}
.resultado{
  border-radius: 10px;
  margin: 5px 15px;
  min-height: 50px;
  max-width: 90%;
}
.textoCalculo{
  background-color: rgb(233, 243, 213);
  min-height: 25px;
  margin: 5px;
}
.textoResultado{
  background-color: rgb(217, 255, 0);
  min-height: 25px;
  margin: 5px;
}
